// ajax axios
import URLs from './URLs';
import { create } from 'axios';
const appkey = 'DuYiyongzhi_1564986206465';
const ajax = create({
  baseURL: URLs.baseURL,
  params: {
    appkey
  }
});

const logon = options => ajax({
  method: 'post',
  url: URLs.logon,
  params: {
    ...options
  }
});

const login = options => ajax({
  method: 'post',
  url: URLs.login,
  params: {
    ...options
  }
});

const getStu = (page, size) => ajax({
  method: 'get',
  url: URLs.findByPage,
  params: {
    page,
    size
  }
});
const updateStu = options => ajax({
  method: 'get',
  url: URLs.updateStu,
  params: {
    ...options
  }
});

const delStu = sNo => ajax({
  method: 'get',
  url: URLs.delStu,
  params: {
    sNo
  }
})

ajax.interceptors.response.use(data => data.data)

export default {
  login,
  logon,
  getStu,
  updateStu,
  delStu
}
