import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login.vue'
import Logon from '@/views/logon.vue'
import Main from '@/views/main.vue'

import stuList from '@/views/main/stuList.vue'
import searchStu from '@/views/main/searchStu.vue'
import addStu from '@/views/main/addStu.vue'

import notFound from '@/views/notFound.vue'

Vue.use(VueRouter)
// 1. 登录
// 2. 注册
// 3. 主页面
// 3.1 学生列表 vuex
// 3.2 添加学生
// 3.3 查找学生
const routes = [
  {
    path: '/',
    beforeEnter(to, from, next) {
      // 登录信息，存在，进入主页面，不存在，进入到登录页面
      var value = localStorage.getItem('username');
      if(value) {
        next('/main')
      } else {
        next('/login')
      }
    }
  },
  {
    path: '/login',
    component: Login,
  },
  {
    path: '/logon',
    component: Logon
  },
  {
    path: '/main',
    component: Main,
    beforeEnter(to, from, next) {
      // 没登陆，就进来，不合适
      var value = localStorage.getItem('username');
      if(value) {
        next();
      } else {
        next('/login')
      }
    },
    redirect: '/main/stulist',
    children: [
      {
        path: 'stulist',
        component: stuList
      },
      {
        path: 'addStu',
        component: addStu
      },
      {
        path: 'searchStu',
        component: searchStu
      }
    ]
  },
  {
    path: '/notFound',
    component: notFound
  },
  {
    path: '*',
    redirect: '/notFound'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkExactActiveClass: 'active',
})
router.beforeEach((to, from, next) => {
  console.log(to);
  next();
})

export default router
